/*----------------------
Slider Default Style 
------------------------*/
.slide {
    .inner {
        padding-top: 80px;
        text-align: center;
        @media #{$sm-layout} {
            padding-top: 70px;
        }
        @media #{$large-mobile} {
            padding-top: 30px;
        }
        h1 {
            &.title {
                font-size: 110px;
                font-weight: 900;
                font-family: 'Montserrat', sans-serif;
                font-weight: 900;
                line-height: 120px;
                @media #{$laptop-device} {
                    font-size: 100px;
                    line-height: 112px;
                }
                @media #{$lg-layout} {
                    font-size: 90px;
                    line-height: 103px;
                }
                @media #{$md-layout} {
                    font-size: 70px;
                    line-height: 82px;
                    margin-bottom: 0;
                }
                @media #{$sm-layout} {
                    font-size: 50px;
                    line-height: 70px;
                    margin-bottom: 0;
                }
                @media #{$large-mobile} {
                    font-size: 34px;
                    line-height: 42px;
                    margin-bottom: 20px;
                }
            }
        }
    }
    &.slide-style-1 {
        .inner {
            padding-top: 100px;
            @media #{$md-layout} {
                padding-top: 50px;
            }
            @media #{$sm-layout} {
                padding-top: 57px;
            }
            @media #{$large-mobile} {
                padding-top: 57px;
            }
            h1 {
                &.title {
                    @media #{$md-layout} {
                        font-size: 68px;
                        line-height: 81px;
                        margin-bottom: 9px;
                    }
                }
            }
        }
        .service-wrapper {
            &.service-white {
                @media #{$lg-layout} {
                    margin-top: 64px;
                }
            }
        }
    }

    &.slide-style-2 {
        padding-top: 230px;
        padding-bottom: 284px;
        overflow: hidden;
        @media #{$laptop-device} {
            padding-top: 150px;
            padding-bottom: 150px;
        }
        

        &.fullscreen {
            min-height: 950px;
            @media #{$laptop-device} {
                padding-top: 160px;
                padding-bottom: 160px;
                min-height: auto;
            }
            @media #{$lg-layout} {
                min-height: auto;
            }
            @media #{$md-layout} {
                min-height: auto;
            }
            @media #{$sm-layout} {
                min-height: auto;
            }
        }

        &.slider-box-content {
            height: 800px;
            @media #{$lg-layout} {
                height: auto;
            }
            @media #{$md-layout} {
                height: auto;
            }
            @media #{$sm-layout} {
                height: auto;
            }
        }

        @media #{$lg-layout} {
            padding-top: 160px;
            padding-bottom: 170px;
            min-height: auto;
        }
        @media #{$md-layout} {
            padding-top: 150px;
            padding-bottom: 150px;
            min-height: auto;
        }

        @media #{$sm-layout} {
            padding-top: 150px;
            padding-bottom: 150px;
            min-height: auto;
        }
        .inner {
            position: relative;
            z-index: 2;
            @media #{$lg-layout} {
                padding-top: 30px;
            }
            @media #{$md-layout} {
                padding-top: 30px;
            }

            @media #{$sm-layout} {
                padding-top: 0;
                text-align: center !important;
            }
            &.text-end {
                @media #{$sm-layout} {
                    text-align: right !important;
                }
            }
           
            
            > span {
                color: #fff;
                font-size: 15px;
                text-transform: uppercase;
                letter-spacing: 4px;
                display: block;
                margin-bottom: 9px;
            }

            h1 {
                &.title {
                    color: #ffffff;
                    font-size: 100px;
                    font-weight: 900;
                    
                    @media #{$lg-layout} {
                        font-size: 72px;
                    }
                    @media #{$md-layout} {
                        font-size: 60px;
                        margin-bottom: 18px;
                    }
                    @media #{$sm-layout} {
                        font-size: 36px;
                        line-height: 49px;
                    }
                }
            }

            p {
                &.description {
                    color: #c6c9d8;
                    font-size: 24px;
                    line-height: 40px;
                    margin: 0;
                    padding: 0 16%;
                    font-weight: 300;
                    @media #{$sm-layout} {
                        padding: 0;
                    }
                    @media #{$md-layout} {
                        padding: 0 10%;
                    }
                }
            }
            .slide-btn {
                margin-top: 50px;
                @media #{$sm-layout} {
                    margin-top: 30px;
                }
            }
            &.text-start {
                p {
                    padding: 0 37% 0 0;
                    @media #{$sm-layout} {
                        padding: 0;
                    }
                    @media #{$md-layout} {
                        padding: 0 3% 0 0;
                    }
                    @media #{$lg-layout} {
                        padding: 0 20% 0 0;
                    }
                    @media #{$sm-layout} {
                        padding: 0 0 0% 0;
                    }
                }
            }
            &.text-end {
                p {
                    padding: 0 0 0% 46%;
                    @media #{$sm-layout} {
                        padding: 0 0 0% 0;
                    }
                }
            }
        }

        &.without-overlay{
            .inner {
                p {
                    &.description {
                        color: #fff;
                        opacity: 0.7;
                    }
                }
            }
        }

        &.slider-paralax{
            @media #{$laptop-device} {
                height: auto;
                padding-top: 150px;
                padding-bottom: 150px;
            }
        }
    }

    &.slider-style-3 {
        .inner {
            > span {
                color: $heading-color;
                font-size: 15px;
                text-transform: uppercase;
                letter-spacing: 4px;
                display: block;
                margin-bottom: 20px;
                &.theme-gradient {
                    display: inline-block;
                }
                @media #{$sm-layout} {
                    font-size: 13px;
                    margin-bottom: 10px;
                }
            }
            h1 {
                &.title {
                    color: $heading-color;
                    font-size: 75px;
                    font-weight: 900;
                    font-family: 'Montserrat', sans-serif;
                    line-height: 90px;
                    text-transform: inherit;
                    width: 70%;

                    @media #{$lg-layout} {
                        font-size: 70px;
                    }

                    @media #{$md-layout} {
                        font-size: 60px;
                        line-height: 77px;
                        width: 85%;
                    }

                    @media #{$sm-layout} {
                        font-size: 42px;
                        line-height: 57px;
                        width: 100%;
                    }

                    span{
                        color: var(--color-primary);
                    }
                }
            }
            .description {
                margin-top: 25px;
            }
        }
    }

    &.slide-style-4 {
        .inner {
            padding-top: 60px;
            h1 {
                &.title {
                    font-size: 80px;
                    font-weight: 700;
                    line-height: 1.2;
                    color: #fff;
                    @media #{$lg-layout} {
                        font-size: 60px;
                    }
                    @media #{$md-layout} {
                        font-size: 50px;
                    }
                    @media #{$sm-layout} {
                        font-size: 40px;
                    }
                    @media #{$small-mobile} {
                        font-size: 32px;
                    }
                }
            }
            .description {
                color: #fff;
                opacity: 0.7;
                font-size: 20px;
                line-height: 1.8;
                margin-top: 30px;
                margin-bottom: 37px;
            }
        }
    }

}

.slider-wrapper {
    &.color-white{
        .inner{
            p{
                &.description{
                    color: #ffffff;
                }
            }
        }
    }
    .service-wrapper{
        @media #{$sm-layout} {
            margin-bottom: -20px;
        }
    }
}

.react-parallax {
    img {
        object-fit: cover;
