/*--------------------------------------
Slider Default Button And Dot STyle
---------------------------------------*/

.slider-activation {
    position: relative;
    .slick-dots {
        bottom: 60px;
        @media #{$sm-layout} {
            bottom: 88px;
        }
    }

    button {
        &.slick-arrow {
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            z-index: 2;
            transition: 0.5s;
            opacity: 1;
            width: 68px;
            height: 68px;
            border: 2px solid rgba(255,255,255, 0.2);
            border-radius: 100%;

            @media #{$md-layout} {
                display: none !important;
            }

            @media #{$lg-layout} {
                display: none !important;
            }
            @media #{$sm-layout} {
                display: none !important;
            }
            &:hover{
                background: var(--color-primary);
                border-color: var(--color-primary);
            }
            &::before {
                line-height: 1;
                opacity: 0.4;
                position: relative;
                top: -2px;
                transition: 0.5s;
                z-index: 2;
            }
            &.slick-prev {
                left: 50px;
                @media #{$sm-layout} {
                    left: 10px;
                }

                &::before{
                    content: url(../images/icons/slick-arrow-left-small.png);
                }
                &:hover{
                    left: 30px;
                    @media #{$sm-layout} {
                        left: 20px;
                    }
                    &::before{
                        opacity: 1;
                    }
                }
            }
            &.slick-next {
                left: auto;
                right: 50px;
                @media #{$sm-layout} {
                    right: 10px;
                }
                &::before{
                    content: url(../images/icons/slick-arrow-right-small.png);
                }
                &:hover {
                    right: 30px;
                    @media #{$sm-layout} {
                        right: 20px;
                    }
                    &::before{
                        opacity: 1;
                    }
                }
            }
        }
    }
}



